export const getZodiac = (birthday) => {
  const month = parseInt(birthday.slice(0, 2), 10);
  const day = parseInt(birthday.slice(2, 4), 10);

  const zodiacSigns = [
    {
      name: " ",
      start: { month: 12, day: 22 },
      end: { month: 1, day: 19 },
    },
    {
      name: " ",
      start: { month: 1, day: 20 },
      end: { month: 2, day: 18 },
    },
    {
      name: " ",
      start: { month: 2, day: 19 },
      end: { month: 3, day: 20 },
    },
    {
      name: " ",
      start: { month: 3, day: 21 },
      end: { month: 4, day: 19 },
    },
    {
      name: " ",
      start: { month: 4, day: 20 },
      end: { month: 5, day: 20 },
    },
    {
      name: " ",
      start: { month: 5, day: 21 },
      end: { month: 6, day: 21 },
    },
    {
      name: " ",
      start: { month: 6, day: 22 },
      end: { month: 7, day: 22 },
    },
    {
      name: " ",
      start: { month: 7, day: 23 },
      end: { month: 8, day: 22 },
    },
    {
      name: " ",
      start: { month: 8, day: 23 },
      end: { month: 9, day: 22 },
    },
    {
      name: " ",
      start: { month: 9, day: 23 },
      end: { month: 10, day: 23 },
    },
    {
      name: " ",
      start: { month: 10, day: 24 },
      end: { month: 11, day: 22 },
    },
    {
      name: " ",
      start: { month: 11, day: 23 },
      end: { month: 12, day: 21 },
    },
  ];

  for (const sign of zodiacSigns) {
    if (
      (month === sign.start.month && day >= sign.start.day) ||
      (month === sign.end.month && day <= sign.end.day)
    ) {
      return sign.name;
    }
  }
  return null;
};
